
import { defineComponent } from 'vue'

export default defineComponent({
  props: {
    dataList: {
      type: Object,
      reuqired: true
    },
    propList: {
      type: Array,
      reuqired: true
    },
    showIndexColumn: {
      type: Boolean,
      defalut: false
    },
    showSelectColumn: {
      type: Boolean,
      defalut: false
    },
    title: {
      type: String,
      defalut: ''
    }
  },
  emits: ['selectionChange'],
  setup(props, { emit }) {
    const handleSelectionChange = (value: any) => {
      emit('selectionChange', value)
    }
    return { handleSelectionChange }
  }
})
